<template>
  <div>
    <el-breadcrumb style="height: 30px" separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>开通商家金融</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 搜索 -->
    <el-card
      shadow="never"
      style="margin-bottom: 6px; height: 80px"
      class="box-card"
    >
      <el-row>
        <el-col :span="5">
          <div style="color: #333; font-size: 15px" class="demo-input-suffix">
            商家手机号：
            <el-input
              style="width: 200px"
              maxlength="11"
              v-model="search.phone"
              placeholder="请输入手机号"
            ></el-input>
          </div>
        </el-col>

        <el-col :span="5">
          <div style="color: #333; font-size: 15px" class="demo-input-suffix">
            商家名称：
            <el-input
              style="width: 200px"
              maxlength="11"
              v-model="search.name"
              placeholder="请输入商家名称"
            ></el-input>
          </div>
        </el-col>

        <el-col :span="5">
          <div style="color: #333; font-size: 15px" class="demo-input-suffix">
            城市：
            <el-input
              style="width: 200px"
              maxlength="11"
              v-model="search.city"
              placeholder="请输入城市名称"
            ></el-input>
            <!-- <el-cascader :clearable="true" :options="city">
              <template slot-scope="{ node, data }">
                <span>{{ data.label }}</span>
                <span v-if="!node.isLeaf"> ({{ data.children.length }}) </span>
              </template>
            </el-cascader> -->
          </div>
        </el-col>

        <el-col :span="5">
          <div style="color: #333; font-size: 15px" class="demo-input-suffix">
            状态：
            <el-select
              style="width: 200px"
              v-model="search.state"
              placeholder="请选择"
            >
              <el-option
                v-for="item in state"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </div>
        </el-col>

        <el-col :span="4">
          <el-button @click="searchEvent" type="primary">查询</el-button>
          <el-button @click="apply" type="danger">申请开通</el-button>
        </el-col>
      </el-row>
    </el-card>
    <!-- 表格 -->
    <el-card shadow="never" class="card_bottom box-card">
      <el-table
        highlight-current-row
        :data="tableData"
        height="calc(100vh - 300px)"
        v-loading="tableLoading"
      >
        <el-table-column fixed prop="groupName" label="商家名称" width="170">
        </el-table-column>
        <el-table-column prop="levelCN" label="等级" width="120">
        </el-table-column>
        <el-table-column prop="telephone" label="商家手机号" width="170">
        </el-table-column>
        <el-table-column prop="proviceCity" label="城市" width="150">
        </el-table-column>
        <el-table-column prop="address" label="地图位置" width="240">
        </el-table-column>
        <el-table-column prop="applicationStatusCN" label="状态" width="90">
        </el-table-column>
        <el-table-column prop="applicationTimeCN" label="申请时间" width="210">
        </el-table-column>
        <el-table-column prop="trueName" label="申请人" width="140">
        </el-table-column>
        <el-table-column label="操作" min-width="120">
          <template>
            <el-button type="text" size="small">查看</el-button>
            <el-button type="text" size="small">编辑</el-button>
            <el-button type="text" size="small">重新发起</el-button>
            <el-button type="text" size="small">日志</el-button>
          </template>
        </el-table-column>
      </el-table>

      <!-- 审核商家小程序弹框 -->
      <el-dialog title="审核处理" width="36%" :visible.sync="dialogFormVisible">
        <el-form style="padding-left: 70px" :model="form">
          <el-form-item
            style="width: 80%"
            label="第三方商户ID："
            :label-width="formLabelWidth"
          >
            <el-input v-model="form.merchantsID" autocomplete="off"></el-input>
          </el-form-item>

          <el-form-item
            style="width: 80%"
            label="用户端小程序ID："
            :label-width="formLabelWidth"
          >
            <el-input v-model="form.userID" autocomplete="off"></el-input>
          </el-form-item>

          <el-form-item
            style="width: 80%"
            label="商家端小程序ID："
            :label-width="formLabelWidth"
          >
            <el-input v-model="form.merchantID" autocomplete="off"></el-input>
          </el-form-item>

          <el-form-item
            style="width: 80%"
            label="备注说明："
            :label-width="formLabelWidth"
          >
            <el-input
              type="textarea"
              placeholder="请输入内容"
              v-model="form.note"
              maxlength="30"
              show-word-limit
            >
            </el-input>
          </el-form-item>
        </el-form>

        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="auditOpen">开通成功</el-button>
          <el-button type="primary" @click="dialogFormVisible = false"
            >开通失败</el-button
          >
        </div>
      </el-dialog>

      <!-- 分页 -->
      <div class="pading">
        <el-pagination background layout="prev, pager, next" :total="paging.pages">
        </el-pagination>
      </div>
    </el-card>
  </div>
</template>
<script>
import { provinces } from "@/utils/city.json";
import {
  getFinanceApplicationList,
  shenHeFinanceApplication,
} from "@/api/openFinancial.js";
export default {
  data() {
    return {
      form: {
        merchantsID: "",
        userID: "",
        merchantID: "",
        note: "",
      },
      formLabelWidth: "125px",
      dialogFormVisible: false,

      search: {
        phone: "",
        name: "",
        city: "",
        state: null,
      },
      city: provinces,
      state: [
        {
          value: null,
          label: "全部",
        },
        {
          value: 0,
          label: "未申请",
        },
        {
          value: 1,
          label: "未申请中",
        },
        {
          value: 2,
          label: "已开通",
        },
        {
          value: 3,
          label: "开通失败",
        },
      ],
      tableLoading: false,
      paging: {},
      tableData: [],
    };
  },
  methods: {
    apply() {
      this.$router.push("/applyOpen");
    },
    // 搜索
    async searchEvent() {
      this.tableLoading = true;
      var n1 = await getFinanceApplicationList({
        groupName: this.search.name,
        city: this.search.city,
        telephone: this.search.phone,
        level: this.search.state,
        pageNumber: 1,
        pageSize: 10,
      });
      this.tableData = n1.data.data;
      this.paging = n1.data.pagination;
      this.tableLoading = false;
    },
    // 开通成功失败
    async auditOpen() {
      var n1 = await shenHeFinanceApplication({
        groupName: this.form.merchantsID,
        city: this.form.userID,
        telephone: this.form.merchantID,
        level: this.form.note,
      });
      console.log(n1);
    },
  },
  async created() {
    // 获取表格数据
    this.tableLoading = true;
    var n1 = await getFinanceApplicationList({
      groupName: "",
      city: "",
      telephone: "",
      level: null,
      pageNumber: 1,
      pageSize: 10
    });

    // var n1 = await getFinanceApplicationList({
    //   groupID: "1000",
    //   groupLevel: 0,
    //   tradeType: 0,
    //   beginTime: "",
    //   endTime: "",
    //   accountType: 1,
    //   groupName: "",
    //   groupPhone: "",
    //   tradeNo: "",
    //   pageNumber: 1,
    //   pageSize: 10,
    // });
    this.tableData = n1.data.data;
    this.paging = n1.data.pagination;
    this.tableLoading = false;
  },
};
</script>
<style scoped>
.card_bottom {
  width: 100%;
  height: calc(100vh - 210px);
  position: relative;
}
.pading {
  position: absolute;
  width: 200px;
  right: 240px;
  bottom: 20px;
}
</style>



