<template>
  <div>
    <el-breadcrumb
      v-if="isShowBreadcrumb == 'applyOpen'"
      style="height: 30px"
      separator-class="el-icon-arrow-right"
    >
      <el-breadcrumb-item :to="{ path: '/openFinancial' }"
        >开通商家金融</el-breadcrumb-item
      >
      <el-breadcrumb-item :to="{ path: '/applyOpen' }"
        >申请开通</el-breadcrumb-item
      >
      <el-breadcrumb-item>选择商家</el-breadcrumb-item>
    </el-breadcrumb>

    <el-breadcrumb
      v-if="isShowBreadcrumb == 'preloaded'"
      style="height: 30px"
      separator-class="el-icon-arrow-right"
    >
      <el-breadcrumb-item :to="{ path: '/merchantsMoney' }"
        >商家资金管理</el-breadcrumb-item
      >
      <el-breadcrumb-item :to="{ path: '/preloaded' }"
        >预充值</el-breadcrumb-item
      >
      <el-breadcrumb-item>选择商家</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- 搜索 -->
    <el-card
      shadow="never"
      style="margin-bottom: 6px; height: 80px"
      class="box-card"
    >
      <el-row>
        <el-col :span="5">
          <div style="color: #333; font-size: 15px" class="demo-input-suffix">
            商家手机号：
            <el-input
              style="width: 200px"
              maxlength="11"
              v-model="search.phone"
              placeholder="请输入手机号"
            ></el-input>
          </div>
        </el-col>

        <el-col :span="5">
          <div style="color: #333; font-size: 15px" class="demo-input-suffix">
            商家名称：
            <el-input
              style="width: 200px"
              maxlength="11"
              v-model="search.name"
              placeholder="请输入商家名称"
            ></el-input>
          </div>
        </el-col>

        <el-col :span="5">
          <div style="color: #333; font-size: 15px" class="demo-input-suffix">
            城市：
            <el-input
              style="width: 200px"
              maxlength="11"
              v-model="search.city"
              placeholder="请输入城市名称"
            ></el-input>
            <!-- <el-cascader :clearable="true" :options="city">
              <template slot-scope="{ node, data }">
                <span>{{ data.label }}</span>
                <span v-if="!node.isLeaf"> ({{ data.children.length }}) </span>
              </template>
            </el-cascader> -->
          </div>
        </el-col>

        <el-col :span="5">
          <div style="color: #333; font-size: 15px" class="demo-input-suffix">
            等级：
            <el-select
              style="width: 200px"
              v-model="search.state"
              placeholder="请选择"
              clearable
            >
              <el-option
                v-for="item in state"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </div>
        </el-col>

        <el-col :span="4">
          <el-button @click="searchEvent" type="primary">查询</el-button>
          <el-button @click="chooseMerchants" type="success">确定</el-button>
        </el-col>
      </el-row>
    </el-card>
    <!-- 表格 -->
    <el-card shadow="never" class="card_bottom box-card">
      <el-table
        highlight-current-row
        :data="tableData"
        height="calc(100vh - 300px)"
        v-loading="tableLoading"
        @row-click="selection"
      >
        <el-table-column fixed prop="groupName" label="商家名称" width="360">
        </el-table-column>
        <el-table-column prop="telephone" label="商家手机号" width="360">
        </el-table-column>
        <el-table-column prop="city" label="所属城市" width="340">
        </el-table-column>
        <el-table-column prop="level" label="等级" min-width="310">
        </el-table-column>
      </el-table>

      <!-- 确定选择按钮 暂定待删 -->
      <!-- <el-button class="determine" type="primary">确定</el-button> -->
      <!-- 分页 -->
      <div class="pading">
        <el-pagination background layout="prev, pager, next" :total="50">
        </el-pagination>
      </div>
    </el-card>
  </div>
</template>
<script>
import { provinces } from "@/utils/city.json";
import { getSysGroupList } from "@/api/openFinancial.js";
export default {
  data() {
    return {
      isShowBreadcrumb: "",
      search: {
        phone: "",
        name: "",
        city: "",
        state: "",
      },
      city: provinces,
      state: [
        {
          value: "受理",
          label: "受理",
        },
        {
          value: "已开通",
          label: "已开通",
        },
        {
          value: "开通失败",
          label: "开通失败",
        },
      ],
      tableLoading: false,
      paging: {},
      tableData: [],
      row_val:''
    };
  },
  methods: {
     // 搜索
    async searchEvent(){
      this.tableLoading = true
      var n1 = await getSysGroupList({
        "groupName": this.search.name,
        "city": this.search.city,
        "telephone": this.search.phone,
        "level": this.search.state,
        "pageNumber": 1,
        "pageSize": 10
      })
      this.tableData = n1.data.data
      this.paging = n1.data.pagination
      this.tableLoading = false
    },
    // 饿了么组件库选中表格某条数据事件
    selection(val){
      console.log(val);
      this.row_val = val
    },
    // 确定选择城市
    chooseMerchants(){
      if(!this.row_val){
        alert('请选择城市')
        return;
      }
      console.log(this.$route.params.page);
      if(this.$route.params.page === 'applyOpen'){
        // 商家开通
        this.$router.push({path:'/applyOpen',query:{merchants:JSON.stringify(this.row_val)}});
      }else if(this.$route.params.page === 'preloaded'){
        // 预充值选择商家
        this.$router.push({path:'/preloaded',query:{merchants:JSON.stringify(this.row_val)}});
      }
    }
  },
  async created() {
    // 判断面包屑展示
    this.isShowBreadcrumb = this.$route.params.page;

    // 获取表格数据
    this.tableLoading = true;
    var n1 = await getSysGroupList({
      groupName: "",
      city: "",
      telephone: "",
      level: 0,
      pageNumber: 1,
      pageSize: 10,
    });
    this.tableData = n1.data.data;
    this.paging = n1.data.pagination;
    this.tableLoading = false;
  },
};
</script>
<style scoped>
.card_bottom {
  width: 100%;
  height: calc(100vh - 210px);
  position: relative;
}
.pading {
  position: absolute;
  width: 200px;
  right: 240px;
  bottom: 20px;
}
/* 确定按钮 暂定待删 */
/* .determine{
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 20px;
} */
</style>



