<template>
  <el-form
    :model="ruleForm"
    :rules="rules"
    ref="ruleForm"
    label-width="100px"
    label-position="right"
    class="demo-ruleForm"
  >
    <el-form-item label-width="170px" label="法人姓名" prop="legalName">
      <el-input v-model="ruleForm.legalName"></el-input>
    </el-form-item>
    <el-form-item label-width="170px" label="法人身份证号" prop="legalCard">
      <el-input v-model="ruleForm.legalCard"></el-input>
    </el-form-item>
    <el-form-item
      label-width="170px"
      label="法人身份证有效期"
      prop="card_time"
      required
    >
      <el-date-picker
        v-model="ruleForm.card_time"
        type="daterange"
        range-separator="至"
        start-placeholder="开始日期"
        end-placeholder="结束日期"
      >
      </el-date-picker>
    </el-form-item>
    <el-form-item label-width="170px" label="法人手机号" prop="legalPhone">
      <el-input v-model="ruleForm.legalPhone"></el-input>
    </el-form-item>
    <el-form-item label-width="170px" label="电子邮箱" prop="legalEmail">
      <el-input v-model="ruleForm.legalEmail"></el-input>
    </el-form-item>

    <!-- 上传数据 -->
    <el-form-item label-width="170px" label="上传法人身份证正反面" required>
      <el-upload
        :action="baseURL+'/Api/V1/SysDocument/UploadFile'"
        name="formFiles"
        :data="{ FileCate: 'IdCard' }"
        :file-list="fileList_one"
        list-type="picture-card"
        :on-success="handleUploadSuccess_one"
        :on-remove="handleRemove_one"
        :on-error="handleUploadError_one"
        :multiple="true"
      >
        <i class="el-icon-plus"></i>
      </el-upload>
      <el-dialog :visible.sync="dialogVisible">
        <img width="100%" :src="dialogImageUrl" alt="" />
      </el-dialog>
    </el-form-item>
    <!-- 提交按钮 -->
    <el-form-item label-width="170px">
      <el-button @click="shang">上一步</el-button>

      <el-button type="primary" @click="submitForm('ruleForm')"
        >下一步</el-button
      >

      <!-- <el-button @click="resetForm('ruleForm')">重置</el-button> -->
    </el-form-item>
  </el-form>
</template>

<script>
import Bus from "@/utils/bus.js";
import { saveFinanceApplication } from "@/api/openFinancial.js";
export default {
  data() {
    return {
      //   表单验证等
      ruleForm: {
        legalName: "1",
        legalCard: "110101199003071292",
        card_time: "",
        legalPhone: "17606146484",
        legalEmail: "328609919@qq.com",
        cardImgList: [],
      },
      fileList_one: [],
      dialogVisible: false,
      dialogImageUrl: "",
      rules: {
        legalName: [
          { required: true, message: "法人姓名不能为空", trigger: "blur" },
        ],
        legalCard: [
          { required: true, message: "法人身份证号不能为空", trigger: "blur" },
          // {
          //   pattern: /^[1-9]\d{5}(18|19|20|(3\d))\d{2}((0[1-9])|(1[0-2]))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$/,
          //   message: "身份证号格式不正确",
          // },
        ],
        card_time: [
          {
            required: true,
            message: "请选择日期",
            trigger: "change",
          },
        ],
        legalPhone: [
          { required: true, message: "法人手机号不能为空", trigger: "blur" },
          // {
          //   pattern: /^1[3456789]\d{9}$/,
          //   message: "手机号格式不正确",
          // },
        ],
        legalEmail: [
          { required: true, message: "电子邮箱不能为空", trigger: "blur" },
          // {
          //   pattern: /^\w+((.\w+)|(-\w+))@[A-Za-z0-9]+((.|-)[A-Za-z0-9]+).[A-Za-z0-9]+$/,
          //   message: "邮箱格式不正确",
          // },
        ],
      },
    };
  },
  created() {
    Bus.$on("formTwo", () => {
      var n1 = JSON.parse(localStorage.getItem("FormData"));
      // 解决饿了么组件的图片不展示问题
      if(n1.data.idCardPicLst){
        for(var i = 0;i<n1.data.idCardPicLst.length;i++){
          n1.data.idCardPicLst[i]['url'] = this.baseURL+'/'+n1.data.idCardPicLst[i].xdPath
        }
      }
      var card_time = [
        n1.data.financeApplication.licenseStartDate,
        n1.data.financeApplication.licenseEndDate,
      ];
      this.ruleForm = {
        legalName: n1.data.financeApplication.corporationName,
        legalCard: n1.data.financeApplication.corporationIdno,
        card_time,
        legalPhone: n1.data.financeApplication.corporationPhone,
        legalEmail: n1.data.financeApplication.corporationEmail,
        idCardPicLst: n1.data.cardImgList,
      };
      this.fileList_one = n1.data.idCardPicLst;
    });
  },
  methods: {
    shang() {
      this.$emit("before", "one");
    },
    submitForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          var data_one = JSON.parse(localStorage.getItem("data_one"));
          // 需要先把数据上传服务器
          var n1 = await saveFinanceApplication({
            uid: data_one.uid,
            // uid: 0,
            groupID: 1000,
            isOver: "N",
            // 第一页
            CompanyName: data_one.merchantsName,
            registeredCapital: data_one.capital,
            businessScope: data_one.scopeBusiness,
            brandName: data_one.brandName,
            businessLicenseCode: data_one.businessLicense,
            CompanyAddress: data_one.businessAddr,
            licenseStartDate: data_one.time[0],
            licenseEndDate: data_one.time[1],
            businessLicensePicLst: data_one.businessList?data_one.businessList:[],
            shopPicLst: data_one.storesList?data_one.storesList:[],
            // 第二页
            corporationName: this.ruleForm.legalName,
            corporationIDNo: this.ruleForm.legalCard,
            corporationIDStartDate: this.ruleForm.card_time[0],
            corporationIDEndDate: this.ruleForm.card_time[1],
            corporationPhone: this.ruleForm.legalPhone,
            corporationEmail: this.ruleForm.legalEmail,
            idCardPicLst: this.ruleForm.cardImgList?this.ruleForm.cardImgList:[],
          });
          this.ruleForm.uid = n1.data.uid;
          localStorage.setItem("data_two", JSON.stringify(this.ruleForm));
          Bus.$emit("formThree");
          this.$emit("jump", "three");
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },

    // 身份证照片上传
    // 上传成功
    handleUploadSuccess_one(res, file, fileList) {
      var n1 = fileList.map((item) => {
        if (item.response) {
          return item.response.data[0];
        } else {
          return item;
        }
      });
      this.ruleForm.cardImgList = n1;
      // 上传成功
      console.log("上传成功");
      console.log(this.ruleForm.cardImgList);
    },
    // 删除
    handleRemove_one(file, fileList) {
      var n1 = fileList.map((item) => {
        if (item.response) {
          return item.response.data[0];
        } else {
          return item;
        }
      });
      this.ruleForm.cardImgList = n1;
      console.log("删除成功");
      console.log(this.ruleForm.cardImgList);
    },
    // 上传异常捕捉
    handleUploadError_one(response, file, fileList) {
      this.$message.error(JSON.parse(response.message).msg);
    },
  },
};
</script>

<style scoped>
/* 上传 */
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 148px;
  height: 148px;
  line-height: 148px;
  text-align: center;
}
.avatar {
  width: 148px;
  height: 148px;
  display: block;
}
</style>


