import request from '@/utils/request'
// get请求



// post请求
// 商家资金列表
export function queryGroupAccount(data) {
    return request({
        url: '/Api/V1/SysGroupAccount/QueryGroupAccount',
        method: 'post',
        data
    })
}

// 钱包明细列表
export function queryQbTrans(data) {
    return request({
        url: '/Api/V1/SysGroupAccount/QueryQbTrans',
        method: 'post',
        data
    })
}

// 调整授信额度
export function saveCreditMoney(data) {
    return request({
        url: '/Api/V1/SysGroupAccount/SaveCreditMoney',
        method: 'post',
        data
    })
}

// 预充值列表
export function queryCzTrans(data) {
    return request({
        url: '/Api/V1/SysGroupAccount/QueryCzTrans',
        method: 'post',
        data
    })
}

// 生成虚拟订单接口
export function getOrder(data) {
    return request({
        url: '/ClientAPITest/APIHandler.ashx?methodName=Finance_SubmitInvest',
        method: 'post',
        data
    })
}


// 微信支付获取字符串接口
export function getString(data) {
    return request({
        url: '/ClientAPITest/APIHandler.ashx?methodName=Buiness_MiniPayFor',
        method: 'post',
        data
    })
}


