<template>
  <div>
    <el-breadcrumb style="height:30px;" separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/openFinancial' }">开通商家金融</el-breadcrumb-item>
      <el-breadcrumb-item>申请开通</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 时间线 -->
    <el-card
      shadow="never"
      style="margin-bottom: 6px; height: 180px"
      class="box-card"
    >
      <el-timeline :reverse="reverse">
        <el-timeline-item
          v-for="(activity, index) in activities"
          :key="index"
          :timestamp="activity.timestamp"
          :color="activity.isComplete ? '#5da2de' : '#e5e7ec'"
        >
          {{ activity.content }}
        </el-timeline-item>
      </el-timeline>
    </el-card>
    <!-- 申请的表单组件 -->
    <el-card shadow="never" class="card_bottom box-card">
      <div class="box">
        <form-one @before="index2" @jump="index" :merchants="merchants" v-show="isShowWho==='one'" />
        <form-two @before="index2" @jump="index" v-show="isShowWho==='two'" />
        <form-three @before="index2" @jump="index" v-show="isShowWho==='three'" />
      </div>
    </el-card>
  </div>
</template>

<script>
  
import formOne from '@/components/applyOpen/one.vue'
import formTwo from '@/components/applyOpen/two.vue'
import formThree from '@/components/applyOpen/three.vue'

export default {
  data() {
    return {
      //时间线
      reverse: false,
      activities: [
        {
          content: "营业执照信息",
          timestamp: "2018-04-15",
          isComplete: true,
        },
        {
          content: "法人信息",
          timestamp: "2018-04-13",
          isComplete: false,
        },
        {
          content: "银行卡信息",
          timestamp: "2018-04-11",
          isComplete: false,
        },
      ],
      // 商家
      merchants:'',
      isShowWho:'one'
    };
  },
  watch:{
    isShowWho(newName, oldName){
      if(newName == 'one'){
        this.activities[0].isComplete = true
        this.activities[1].isComplete = false
        this.activities[2].isComplete = false
      }else if(newName == 'two'){
        this.activities[0].isComplete = true
        this.activities[1].isComplete = true
        this.activities[2].isComplete = false
      }else if(newName == 'three'){
        this.activities[0].isComplete = true
        this.activities[1].isComplete = true
        this.activities[2].isComplete = true
      }
    }
  },
  components:{
    'form-one':formOne,
    'form-two':formTwo,
    'form-three':formThree
  },
  created(){
    this.merchants = this.$route.query.merchants
  },
  methods: {
    index(e){
      this.isShowWho = e
    },
    index2(e){
      this.isShowWho = e
    }
  },
};
</script>

<style scoped>
.card_bottom {
  width: 100%;
  height: calc(100vh - 310px);
  overflow: auto;
  position: relative;
}
.box {
  width: 600px;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

</style>


