<template>
  <el-form
    :model="ruleForm"
    :rules="rules"
    ref="ruleForm"
    label-width="100px"
    label-position="right"
    class="demo-ruleForm"
  >
    <el-form-item label-width="140px" label="开户行所在省市" prop="province">
      <el-input v-model="ruleForm.province"></el-input>
    </el-form-item>
    <el-form-item label-width="140px" label="开户银行" prop="bank">
      <el-input v-model="ruleForm.bank"></el-input>
    </el-form-item>
    <el-form-item label-width="140px" label="开户行名称" prop="bankName">
      <el-input v-model="ruleForm.bankName"></el-input>
    </el-form-item>
    <el-form-item label-width="140px" label="户名" prop="accountName">
      <el-input v-model="ruleForm.accountName"></el-input>
    </el-form-item>
    <el-form-item label-width="140px" label="账户" prop="account">
      <el-input v-model="ruleForm.account"></el-input>
    </el-form-item>

    <!-- 上传数据 -->
    <el-form-item label-width="140px" label="上传银行卡证明" required>
      <el-upload
        :action="baseURL+'/Api/V1/SysDocument/UploadFile'"
        name="formFiles"
        :data="{ FileCate: 'BankCard' }"
        :file-list="fileList_one"
        list-type="picture-card"
        :on-success="handleUploadSuccess_one"
        :on-remove="handleRemove_one"
        :on-error="handleUploadError_one"
        :multiple="true"
      >
        <i class="el-icon-plus"></i>
      </el-upload>
      <el-dialog :visible.sync="dialogVisible">
        <img width="100%" :src="dialogImageUrl" alt="" />
      </el-dialog>
    </el-form-item>

    <el-form-item label-width="140px" label="上传其他材料" required>
      <el-upload
        :action="baseURL+'/Api/V1/SysDocument/UploadFile'"
        name="formFiles"
        :data="{ FileCate: 'CorporationOther' }"
        :file-list="fileList_two"
        list-type="picture-card"
        :on-success="handleUploadSuccess_two"
        :on-remove="handleRemove_two"
        :on-error="handleUploadError_two"
        :multiple="true"
      >
        <i class="el-icon-plus"></i>
      </el-upload>
      <el-dialog :visible.sync="dialogVisible">
        <img width="100%" :src="dialogImageUrl" alt="" />
      </el-dialog>
    </el-form-item>
    <!-- 提交按钮 -->
    <el-form-item label-width="140px">
      <el-button @click="submitForm('ruleForm')">上一步</el-button>
      <el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
      <!-- <el-button @click="resetForm('ruleForm')">重置</el-button> -->
    </el-form-item>
  </el-form>
</template>

<script>
import { saveFinanceApplication } from "@/api/openFinancial.js";
import Bus from "@/utils/bus.js";
export default {
  data() {
    return {
      //   表单验证等
      ruleForm: {
        province: "1",
        bank: "1",
        bankName: "1",
        accountName: "1",
        account: "1",
        bankImgList: [],
        otherImgList: [],
      },
      fileList_one: [],
      fileList_two: [],
      dialogImageUrl: "",
      dialogVisible: false,
      rules: {
        province: [
          {
            required: true,
            message: "开户行所在省市不能为空",
            trigger: "blur",
          },
        ],
        bank: [
          { required: true, message: "开户银行不能为空", trigger: "blur" },
        ],
        bankName: [
          { required: true, message: "开户行名称不能为空", trigger: "blur" },
        ],
        accountName: [
          { required: true, message: "户名不能为空", trigger: "blur" },
        ],
        account: [{ required: true, message: "账户不能为空", trigger: "blur" }],
      },
    };
  },
  created() {
    Bus.$on("formThree", () => {
      var n1 = JSON.parse(localStorage.getItem("FormData"));
      // 解决饿了么组件的图片不展示问题
      if(n1.data.bankCardPicLst){
        for(var i = 0;i<n1.data.bankCardPicLst.length;i++){
          n1.data.bankCardPicLst[i]['url'] = this.baseURL+'/'+n1.data.bankCardPicLst[i].xdPath
        }
      }
      // 解决饿了么组件的图片不展示问题
      if(n1.data.corporationOtherPicLst){
        for(var i = 0;i<n1.data.corporationOtherPicLst.length;i++){
          n1.data.corporationOtherPicLst[i]['url'] = this.baseURL+'/'+n1.data.corporationOtherPicLst[i].xdPath
        }
      }
      var card_time = [
        n1.data.financeApplication.licenseStartDate,
        n1.data.financeApplication.licenseEndDate,
      ];
      this.ruleForm = {
        province: n1.data.financeApplication.bankProvince,
        bank: n1.data.financeApplication.bankType,
        bankName: n1.data.financeApplication.bankName,
        accountName: n1.data.financeApplication.bankAccountName,
        account: n1.data.financeApplication.bankAccountNo,
        bankImgList: [],
        otherImgList: [],
      };
      this.fileList_one = n1.data.bankCardPicLst;
      this.fileList_two = n1.data.corporationOtherPicLst;
    });
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          // 需要先把数据上传服务器
          var data_one = JSON.parse(localStorage.getItem("data_one"));
          var data_two = JSON.parse(localStorage.getItem("data_two"));
          var n1 = await saveFinanceApplication({
            // uid: 0,
            uid: data_two.uid,
            groupID: 1000,
            isOver: "Y",
            // 第一页
            CompanyName: data_one.merchantsName,
            registeredCapital: data_one.capital,
            businessScope: data_one.scopeBusiness,
            brandName: data_one.brandName,
            businessLicenseCode: data_one.businessLicense,
            CompanyAddress: data_one.businessAddr,
            licenseStartDate: data_one.time[0],
            licenseEndDate: data_one.time[1],
            businessLicensePicLst: data_one.businessList?data_one.businessList:[],
            shopPicLst: data_one.storesList?data_one.storesList:[],
            // 第二页
            corporationName: data_two.legalName,
            corporationIDNo: data_two.legalCard,
            corporationIDStartDate: data_two.card_time[0],
            corporationIDEndDate: data_two.card_time[1],
            corporationPhone: data_two.legalPhone,
            corporationEmail: data_two.legalEmail,
            idCardPicLst: data_two.cardImgList?data_two.cardImgList:[],
            // 第三页
            bankType: this.ruleForm.bank ? this.ruleForm.bank : "无",
            bankName: this.ruleForm.bankName,
            bankProvince: this.ruleForm.province,
            bankAccountName: this.ruleForm.accountName,
            bankAccountNo: this.ruleForm.account,
            bankCardPicLst: this.ruleForm.bankImgList?this.ruleForm.bankImgList:[],
            corporationOtherPicLst: this.ruleForm.otherImgList?this.ruleForm.otherImgList:[],
          });

          alert("结束");
        } else {
          this.$emit("before", "two");
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    // 银行卡证明上传
    // 上传成功
    handleUploadSuccess_one(res, file, fileList) {
      var n1 = fileList.map((item) => {
        if (item.response) {
          return item.response.data[0];
        } else {
          return item;
        }
      });
      this.ruleForm.bankImgList = n1;
      // 上传成功
      console.log("上传成功");
      console.log(this.ruleForm.bankImgList);
    },
    // 删除
    handleRemove_one(file, fileList) {
      var n1 = fileList.map((item) => {
        if (item.response) {
          return item.response.data[0];
        } else {
          return item;
        }
      });
      this.ruleForm.bankImgList = n1;
      console.log("删除成功");
      console.log(this.ruleForm.bankImgList);
    },
    // 上传异常捕捉
    handleUploadError_one(response, file, fileList) {
      this.$message.error(JSON.parse(response.message).msg);
    },

    // 其他资料上传
    // 上传成功
    handleUploadSuccess_two(res, file, fileList) {
      var n1 = fileList.map((item) => {
        if (item.response) {
          return item.response.data[0];
        } else {
          return item;
        }
      });
      this.ruleForm.otherImgList = n1;
      // 上传成功
      console.log("上传成功");
      console.log(this.ruleForm.otherImgList);
    },
    // 删除
    handleRemove_two(file, fileList) {
      var n1 = fileList.map((item) => {
        if (item.response) {
          return item.response.data[0];
        } else {
          return item;
        }
      });
      this.ruleForm.otherImgList = n1;
      console.log("删除成功");
      console.log(this.ruleForm.otherImgList);
    },
    // 上传异常捕捉
    handleUploadError_two(response, file, fileList) {
      this.$message.error(JSON.parse(response.message).msg);
    },
  },
};
</script>

<style scoped>
/* 上传 */
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 148px;
  height: 148px;
  line-height: 148px;
  text-align: center;
}
.avatar {
  width: 148px;
  height: 148px;
  display: block;
}
</style>


