<template>
  <div>
    <el-dialog title="查看" :visible.sync="dialogFormVisible">
      <el-card style="margin-bottom: 20px" class="box-card">
        <div slot="header" class="clearfix">
          <span>申请信息</span>
          <el-button style="float: right; padding: 3px 0" type="text"
            >状态：提现中</el-button
          >
        </div>
        <el-row>
          <el-col :span="12">
            <el-form class="oneform" label-width="120px">
              <el-form-item label="提现商家：">
                <span>xxx经销商</span>
              </el-form-item>
              <el-form-item label="提现金额：">
                <span style="color: red">￥200.00</span>
              </el-form-item>
              <el-form-item label="申请提现时间：">
                <span>2020-05-20 12：23：34</span>
              </el-form-item>
            </el-form>
          </el-col>
          <el-col :span="12">
            <el-form label-width="120px">
              <el-form-item label="提现银行：">
                <span>中国银行</span>
              </el-form-item>
              <el-form-item label="银行卡号：">
                <span>62232267656655232</span>
              </el-form-item>
              <el-form-item label="备注信息：">
                <span>无</span>
              </el-form-item>
            </el-form>
          </el-col>
        </el-row>
      </el-card>

      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span>处理信息</span>
        </div>
        <el-row>
          <el-col :span="12">
            <el-form label-width="120px">
              <el-form-item label="处理结果：">
                <el-select v-model="value" placeholder="请选择">
                  <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="到账金额：">
                <el-input
                  v-model="input"
                  placeholder="最大可输入200.00元"
                ></el-input>
              </el-form-item>
              <el-form-item label="差额部分：">
                <el-select v-model="value" placeholder="请选择">
                  <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="处理说明：">
                <el-input
                  type="textarea"
                  placeholder="请输入内容"
                  v-model="textarea"
                  maxlength="30"
                  show-word-limit
                >
                </el-input>
              </el-form-item>
            </el-form>
          </el-col>
          <el-col :span="12"> </el-col>
        </el-row>
      </el-card>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogFormVisible = false"
          >关闭</el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
import Bus from '@/utils/bus.js'
export default {
  data() {
    return {
      dialogFormVisible: false,
      textarea: "",
      input: "",
      options: [
        {
          value: "选项1",
          label: "黄金糕",
        },
        {
          value: "选项2",
          label: "双皮奶",
        },
        {
          value: "选项3",
          label: "蚵仔煎",
        },
        {
          value: "选项4",
          label: "龙须面",
        },
        {
          value: "选项5",
          label: "北京烤鸭",
        },
      ],
    };
  },
  created(){
      Bus.$on('chuli',()=>{
          this.dialogFormVisible = true
      })
  }
};
</script>

<style scoped>
.oneform .el-form-item {
  margin-bottom: 0 !important;
}
.dialog-footer {
  text-align: center !important;
}
.el-button {
  width: 140px;
}
.el-dialog__header {
  background-color: #5da2de;
}
</style>


