<template>
  <div>
    <el-breadcrumb style="height: 30px" separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>商家资金管理</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 搜索 -->
    <el-card
      shadow="never"
      style="margin-bottom: 6px; height: 80px"
      class="box-card"
    >
      <el-row>
        <el-col :span="5">
          <div style="color: #333; font-size: 15px" class="demo-input-suffix">
            商家手机号：
            <el-input
              style="width: 200px"
              maxlength="11"
              v-model="search.phone"
              placeholder="请输入手机号"
            ></el-input>
          </div>
        </el-col>

        <el-col :span="5">
          <div style="color: #333; font-size: 15px" class="demo-input-suffix">
            商家名称：
            <el-input
              style="width: 200px"
              maxlength="11"
              v-model="search.name"
              placeholder="请输入商家名称"
            ></el-input>
          </div>
        </el-col>

        <el-col :span="5">
          <div style="color: #333; font-size: 15px" class="demo-input-suffix">
            商家等级：
            <el-select
              style="width: 200px"
              v-model="search.level"
              placeholder="请选择"
            >
              <el-option
                v-for="item in level"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </div>
        </el-col>

        <el-col :span="6">

          <!-- <div style="color: #333; font-size: 15px" class="demo-input-suffix">
            充值时间：
            <el-date-picker
              v-model="search.time"
              style="width: 240px"
              type="daterange"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
            >
            </el-date-picker>
          </div> -->

          <div class="block">
            <span class="demonstration">充值时间：</span>
            <el-date-picker
              v-model="search.time"
              type="datetimerange"
              range-separator="至"
              style="width: 240px"
              start-placeholder="开始日期"
              value-format="yyyy-MM-dd HH:mm"
              end-placeholder="结束日期">
            </el-date-picker>
          </div>

        </el-col>

        <el-col :span="2.1">
          <el-button @click="searchEvent" type="primary">查询</el-button>
          <el-button @click="ajax" type="primary">导出</el-button>
        </el-col>
      </el-row>
    </el-card>
    <!-- 表格 -->
    <el-card shadow="never" class="card_bottom box-card">
      <!-- 表格 -->
      <el-table
        highlight-current-row
        max-height="700"
        :data="tableData"
        @current-change="index1"
        height="calc(100vh - 300px)"
        v-loading="tableLoading"
      >
        <el-table-column fixed prop="groupName" label="商家名称" width="150">
        </el-table-column>
        <el-table-column prop="level" label="商家等级" width="140">
        </el-table-column>
        <el-table-column prop="qbMoney" label="当前钱包余额" width="140">
        </el-table-column>
        <el-table-column prop="transMoney1" label="累计钱包收入" width="140">
        </el-table-column>
        <el-table-column
          prop="czMoney"
          label="当前预充值余额"
          width="140"
        >
        </el-table-column>
        <el-table-column
          prop="transMoney0"
          label="累计预充值余额"
          width="140"
        >
        </el-table-column>
        <el-table-column prop="creditMoeny" label="授信额度" width="130">
        </el-table-column>
        <el-table-column prop="addTimeString" label="最近充值时间" width="230">
        </el-table-column>
        <el-table-column label="操作" min-width="120">
          <template slot-scope="scope">
            <el-button
              @click="jumpPage('/walletDetail/' + scope.row.groupID)"
              type="text"
              size="small"
              >钱包明细</el-button
            >
            <el-button
              @click="jumpPage('/preloaded/' + scope.row.uid)"
              type="text"
              size="small"
              >预充值</el-button
            >
            <el-button
              @click="(dialogFormVisible = true), (rowData = scope)"
              type="text"
              size="small"
              >授信额度</el-button
            >
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页 -->
      <div class="pading">
        <el-pagination
          @current-change="pagingEvent"
          background
          layout="prev, pager, next"
          :total="paging.pages"
        >
          <!-- <el-pagination @current-change="pagingEvent" background layout="prev, pager, next" :total="50"> -->
        </el-pagination>
      </div>
    </el-card>

    <!-- 授信额度弹框 -->
    <el-dialog width="600px" title="授信额度" :visible.sync="dialogFormVisible">
      <el-form style="width: 400px" :model="form">
        <el-form-item label="所属商家：" :label-width="formLabelWidth">
          <el-input
            :disabled="true"
            v-model="form.shangjia"
            autocomplete="off"
          ></el-input>
        </el-form-item>

        <el-form-item label="当前授信额度：" :label-width="formLabelWidth">
          <el-input
            :disabled="true"
            v-model="form.shouxinedu"
            autocomplete="off"
          ></el-input>
        </el-form-item>

        <el-form-item label="调整授信额度：" :label-width="formLabelWidth">
          <el-input v-model="form.tiaozheng" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="saveCreditMoneyEvent"
          >确 定</el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { queryGroupAccount, saveCreditMoney } from "@/api/merchantsMoney.js";
export default {
  name: "test-keep-alive",
  data() {
    return {
      search: {
        phone: "",
        name: "",
        level: null,
        time: "",
      },
      rowData: {},
      level: [
        {
          value: null,
          label: "全部",
        },
        {
          value: 1,
          label: "一级（主）",
        },
        {
          value: 2,
          label: "一级（子）",
        },
        {
          value: 3,
          label: "二级",
        },
      ],
      tableLoading: false,
      tableData: [
        {
          name: "江苏常州代理点",
          level: "二级代理",
          province: "17606146484",
          walletBalance: "10.00",
          walletIncome: "120390.00",
          advanceWalletBalance: "100.00",
          cumulativeWalletBalance: "100.00",
          creditLines: "20000.00",
          topUpTime: "2016-05-04",
        },
      ],
      paging: {},
      dialogFormVisible: false,
      form: {
        shangjia: "江苏常州",
        shouxinedu: "1000.00",
        tiaozheng: 0,
      },
      formLabelWidth: "120px",
    };
  },
  async created() {
    console.log("调试接口start");
    this.tableLoading = true;
    var n1 = await queryGroupAccount({
      groupName: "",
      telephone: "",
      level: 0,
      starttime: "",
      endtime: "",
      pageNumber: 1,
      pageSize: 10,
    });
    this.tableData = n1.data.data;
    this.paging = n1.data.pagination;
    this.tableLoading = false;

    // 监听hash值变化
    window.onhashchange = function () {
      var tableScroll = document.querySelector(".el-table__body-wrapper");
      var n1 = localStorage.getItem("merchantsMoney_table_scrollHeight");
      tableScroll.scrollTop = n1;
    };
    // for (var i = 0; i < 20; i++) {
    //   this.tableData.push({
    //     name: "江苏常州代理点",
    //     level: "二级代理",
    //     province: "17606146484",
    //     walletBalance: "10.00",
    //     walletIncome: "120390.00",
    //     advanceWalletBalance: "100.00",
    //     cumulativeWalletBalance: "100.00",
    //     creditLines: "20000.00",
    //     topUpTime: "2016-05-04",
    //   });
    // }
  },

  methods: {
    // 测试
    ajax() {
      this.$axios
        .post("/Api/V1/SysGroupAccount/GetTagDictionary", {})
        .then((res) => {
          console.log(res);
        });
    },

    // 跳转页面
    jumpPage(e) {
      var tableScroll = document.querySelector(".el-table__body-wrapper");
      console.dir(tableScroll);
      localStorage.setItem(
        "merchantsMoney_table_scrollHeight",
        tableScroll.scrollTop
      );
      this.$router.push(e);
    },
    index1() {
      var tableScroll = document.querySelector(".el-table__body-wrapper");
      console.dir(tableScroll);
      localStorage.setItem(
        "merchantsMoney_table_scrollHeight",
        tableScroll.scrollTop
      );
    },
    // 分页
    pagingEvent(e) {
      // e就是当前页码
      console.log(e);
    },
    // 搜索
    async searchEvent() {
      console.log(this.search);
      this.tableLoading = true;
      var n1 = await queryGroupAccount({
        groupName: this.search.name,
        telephone: this.search.phone,
        level: this.search.level,
        starttime: this.search.time[0],
        endtime: this.search.time[1],
        pageNumber: 1,
        pageSize: 10,
      });
      this.tableData = n1.data.data;
      this.paging = n1.data.pagination;
      this.tableLoading = false;
    },
    // 调整授信额度
    async saveCreditMoneyEvent() {
      console.log(this.rowData.row.groupID);
      var n1 = await saveCreditMoney({
        // "accountID": String(this.rowData.row.groupID),
        accountID: 1000,
        creditmoeny: this.form.shouxinedu,
      });
      console.log(n1);
    },
  },
};
</script>

<style scoped>
.card_bottom {
  width: 100%;
  height: calc(100vh - 210px);
  position: relative;
}
.pading {
  position: absolute;
  width: 200px;
  right: 240px;
  bottom: 20px;
}
</style>


