<template>
  <div>
    <el-dialog title="查看" :visible.sync="dialogFormVisible">
      <el-card style="margin-bottom: 20px" class="box-card">
        <div slot="header" class="clearfix">
          <span>申请信息</span>
          <el-button style="float: right; padding: 3px 0" type="text"
            >状态：提现中</el-button
          >
        </div>
        <el-row>
          <el-col :span="12">
            <el-form label-width="120px">
              <el-form-item label="提现商家：">
                <span>xxx经销商</span>
              </el-form-item>
              <el-form-item label="提现金额：">
                <span style="color: red">￥200.00</span>
              </el-form-item>
              <el-form-item label="申请提现时间：">
                <span>2020-05-20 12：23：34</span>
              </el-form-item>
            </el-form>
          </el-col>
          <el-col :span="12">
            <el-form label-width="120px">
              <el-form-item label="提现银行：">
                <span>中国银行</span>
              </el-form-item>
              <el-form-item label="银行卡号：">
                <span>62232267656655232</span>
              </el-form-item>
              <el-form-item label="备注信息：">
                <span>无</span>
              </el-form-item>
            </el-form>
          </el-col>
        </el-row>
      </el-card>

      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span>处理信息</span>
        </div>
        <el-row>
          <el-col :span="12">
            <el-form label-width="120px">
              <el-form-item label="处理结果：">
                <span>提现成功</span>
              </el-form-item>
              <el-form-item label="到账金额：">
                <span style="color: red">￥190.00</span>
              </el-form-item>
              <el-form-item label="差额部分：">
                <span>退还给甲方</span>
              </el-form-item>
            </el-form>
          </el-col>
          <el-col :span="12">
            <el-form label-width="120px">
              <el-form-item label="处理方：">
                <span>易骑平台</span>
              </el-form-item>
              <el-form-item label="处理说明：">
                <span>无</span>
              </el-form-item>
              <el-form-item label="处理时间：">
                <span>2020-05-20 12：23：34</span>
              </el-form-item>
            </el-form>
          </el-col>
        </el-row>
      </el-card>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogFormVisible = false">关闭</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import Bus from '@/utils/bus.js'
export default {
  data() {
    return {
      dialogFormVisible: false,
    };
  },
  created(){
      Bus.$on('chakan',()=>{
          this.dialogFormVisible = true
      })
  }
};
</script>

<style scoped>
.el-form-item {
  margin-bottom: 0 !important;
}
.dialog-footer{
    text-align: center !important;
}
.el-button{
    width: 140px;
}
.el-dialog__header{
    background-color: #5da2de;

}
</style>


