<template>
  <el-form
    :model="ruleForm"
    :rules="rules"
    ref="ruleForm"
    label-width="100px"
    label-position="right"
    class="demo-ruleForm"
  >
    <el-form-item label-width="130px" label="选择商家" prop="merchants">
      <!-- <span v-if="merchants">{{JSON.parse(merchants).groupName}}</span> -->

      <el-row :gutter="20">
        <el-col style="padding-left:0;" v-if="merchants" :span="14">
          <el-input :disabled="true" v-model="JSON.parse(merchants).groupName"></el-input>
        </el-col>
        <el-col :span="4">
          <el-button @click="chooseMerchants" type="primary">选择商家</el-button>
        </el-col>
      </el-row>

    </el-form-item>

    <el-form-item label-width="130px" label="商户名称" prop="merchantsName">
      <el-input v-model="ruleForm.merchantsName"></el-input>
    </el-form-item>
    <el-form-item label-width="130px" label="注册资本" prop="capital">
      <el-input v-model="ruleForm.capital"></el-input>
    </el-form-item>
    <el-form-item label-width="130px" label="经营范围" prop="sc4peBusiness">
      <el-input v-model="ruleForm.scopeBusiness"></el-input>
    </el-form-item>
    <el-form-item label-width="130px" label="品牌名称" prop="brandName">
      <el-input v-model="ruleForm.brandName"></el-input>
    </el-form-item>
    <el-form-item label-width="130px" label="商户地址" prop="businessAddr">
      <el-input v-model="ruleForm.businessAddr"></el-input>
    </el-form-item>
    <el-form-item
      label-width="130px"
      label="营业执照编码"
      prop="businessLicense"
    >
      <el-input v-model="ruleForm.businessLicense"></el-input>
    </el-form-item>
    <el-form-item
      label-width="130px"
      label="营业执照有效期"
      required
      prop="time"
    >
      <el-date-picker
        v-model="ruleForm.time"
        type="daterange"
        :editable="true"
        range-separator="至"
        start-placeholder="开始日期"
        end-placeholder="结束日期"
      >
      </el-date-picker>
    </el-form-item>

    <!-- 上传数据 -->
    <el-form-item label-width="130px" label="上传营业执照" required>
      <el-upload
        :action="baseURL+'/Api/V1/SysDocument/UploadFile'"
        name="formFiles"
        :file-list="fileList_one"
        :data="{ FileCate: 'BusinessLicense' }"
        list-type="picture-card"
        :on-success="handleUploadSuccess_one"
        :on-remove="handleRemove_one"
        :on-error="handleUploadError_one"
        :multiple="true"
      >
        <i class="el-icon-plus"></i>
      </el-upload>
      <el-dialog :visible.sync="dialogVisible">
        <img width="100%" :src="dialogImageUrl" alt="" />
      </el-dialog>
    </el-form-item>

    <el-form-item label-width="130px" label="上传门店照片" required>
      <el-upload
        :action="baseURL+'/Api/V1/SysDocument/UploadFile'"
        name="formFiles"
        :data="{ FileCate: 'Shop' }"
        :file-list="fileList_two"
        list-type="picture-card"
        :on-success="handleUploadSuccess_two"
        :on-remove="handleRemove_two"
        :on-error="handleUploadError_two"
        :multiple="true"
      >
        <i class="el-icon-plus"></i>
      </el-upload>
      <el-dialog :visible.sync="dialogVisible">
        <img width="100%" :src="dialogImageUrl" alt="" />
      </el-dialog>
    </el-form-item>
    <!-- 提交按钮 -->
    <el-form-item label-width="130px">
      <el-button type="primary" @click="submitForm('ruleForm')"
        >下一步</el-button
      >
    </el-form-item>
  </el-form>
</template>

<script>
import Bus from "@/utils/bus.js";
import {
  saveFinanceApplication,
  getFinanceApplicationInfoByPost,
} from "@/api/openFinancial.js";
export default {
  props:['merchants'],
  data() {
    return {
      //表单验证等
      ruleForm: {
        merchants:'',
        merchantsName: "2",
        capital: "2",
        scopeBusiness: "2",
        brandName: "2",
        businessAddr: "2",
        businessLicense: "2",
        time: "",
        // 营业执照数组
        businessList: [],
        // 门店照片
        storesList: [],
      },
      fileList_one: [],
      fileList_two: [],
      dialogImageUrl: "",
      dialogVisible: false,
      uid:'',
      rules: {
        merchantsName: [
          { required: true, message: "商家名称不能为空", trigger: "blur" },
        ],
        capital: [
          { required: true, message: "注册资本不能问空", trigger: "blur" },
        ],
        scopeBusiness: [
          { required: true, message: "经营范围不能问空", trigger: "blur" },
        ],
        brandName: [{ required: false }],
        businessAddr: [
          { required: true, message: "商户地址不能为空", trigger: "blur" },
        ],
        businessLicense: [
          { required: true, message: "营业执照编码不能为空", trigger: "blur" },
        ],
        time: [
          {
            required: true,
            message: "请选择日期",
            trigger: "change",
          },
        ],
      },
    };
  },
  async created() {
    var n1 = await getFinanceApplicationInfoByPost({
      groupId: 1009,
    });
    if(!n1.data){
      return;
    }
    localStorage.setItem("FormData", JSON.stringify(n1));
    var time = [
      n1.data.financeApplication.licenseStartDate,
      n1.data.financeApplication.licenseEndDate,
    ];
    // 获取到uid  存储
    if(n1.data.financeApplication.financeApplicationId){
      this.uid = n1.data.financeApplication.financeApplicationId
    }
    // 解决饿了么组件的图片不展示问题
    if(n1.data.businessLicensePicLst){
      for(var i = 0;i<n1.data.businessLicensePicLst.length;i++){
        n1.data.businessLicensePicLst[i]['url'] = this.baseURL+'/'+n1.data.businessLicensePicLst[i].xdPath
      }
    }
    if(n1.data.shopPicLst){
      for(var i = 0;i<n1.data.shopPicLst.length;i++){
        n1.data.shopPicLst[i]['url'] = this.baseURL+'/'+n1.data.shopPicLst[i].xdPath
      }
    }
    
    this.ruleForm = {
      merchantsName: n1.data.financeApplication.CompanyName,
      capital: n1.data.financeApplication.registeredCapital,
      scopeBusiness: n1.data.financeApplication.businessScope,
      brandName: n1.data.financeApplication.brandName,
      businessAddr: n1.data.financeApplication.businessLicenseCode,
      businessLicense: n1.data.financeApplication.businessLicenseCode,
      time,
      // 营业执照数组
      businessList: n1.data.businessLicensePicLst,
      // 门店照片
      storesList: n1.data.shopPicLst,
    };
    this.fileList_one = n1.data.businessLicensePicLst;
    this.fileList_two = n1.data.shopPicLst;
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          // 需要先把数据上传服务器
          var n1 = await saveFinanceApplication({
            uid: this.uid ? this.uid : 0,
            groupID: 1009,
            isOver: "N",
            CompanyName: this.ruleForm.merchantsName,
            registeredCapital: this.ruleForm.capital,
            businessScope: this.ruleForm.scopeBusiness,
            brandName: this.ruleForm.brandName,
            businessLicenseCode: this.ruleForm.businessLicense,
            CompanyAddress: this.ruleForm.businessAddr,
            licenseStartDate: this.ruleForm.time[0],
            licenseEndDate: this.ruleForm.time[1],
            businessLicensePicLst: this.ruleForm.businessList?this.ruleForm.businessList:[],
            shopPicLst: this.ruleForm.storesList?this.ruleForm.storesList:[],
          });
          this.ruleForm.uid = n1.data.uid;
          // 表单数据存储本地
          localStorage.setItem("data_one", JSON.stringify(this.ruleForm));
          this.$emit("jump", "two");
          Bus.$emit("formTwo");
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    // 选择商家跳转
    chooseMerchants() {
      this.$router.push("/chooseMerchants/applyOpen");
    },

    // 营业执照上传
    // 上传成功
    handleUploadSuccess_one(res, file, fileList) {
      var n1 = fileList.map((item) => {
        if (item.response) {
          return item.response.data[0];
        } else {
          return item;
        }
      });
      this.ruleForm.businessList = n1;
      // 上传成功
      console.log("上传成功");
      console.log(this.ruleForm.businessList);
    },
    // 删除
    handleRemove_one(file, fileList) {
      var n1 = fileList.map((item) => {
        if (item.response) {
          return item.response.data[0];
        } else {
          return item;
        }
      });

      this.ruleForm.businessList = n1;
      console.log("删除成功");
      console.log(this.ruleForm.businessList);
    },
    // 上传异常捕捉
    handleUploadError_one(response, file, fileList) {
      this.$message.error(JSON.parse(response.message).msg);
    },

    // 营业执照上传
    // 上传成功
    handleUploadSuccess_two(res, file, fileList) {
      var n1 = fileList.map((item) => {
        if (item.response) {
          return item.response.data[0];
        } else {
          return item;
        }
      });
      this.ruleForm.storesList = n1;
      // 上传成功
      console.log("上传成功");
      console.log(this.ruleForm.storesList);
    },
    // 删除
    handleRemove_two(file, fileList) {
      var n1 = fileList.map((item) => {
        if (item.response) {
          return item.response.data[0];
        } else {
          return item;
        }
      });
      this.ruleForm.storesList = n1;
      console.log("删除成功");
      console.log(this.ruleForm.storesList);
    },
    // 上传异常捕捉
    handleUploadError_two(response, file, fileList) {
      this.$message.error(JSON.parse(response.message).msg);
    },
  },
};
</script>

<style scoped>
/* 上传 */
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 148px;
  height: 148px;
  line-height: 148px;
  text-align: center;
}
.avatar {
  width: 148px;
  height: 148px;
  display: block;
}
</style>


