import Vue from 'vue'
import Router from 'vue-router'
import home from '@/views/index.vue'
import login from '@/views/login.vue'

// 金融管理===开通商家金融
import openFinancial from '@/views/businessFinancial/openFinancial/openFinancial.vue'
import applyOpen from '@/views/businessFinancial/openFinancial/applyOpen.vue'
import chooseMerchants from '@/views/businessFinancial/openFinancial/chooseMerchants.vue'
// 金融管理===商家资金管理
import merchantsMoney from '@/views/businessFinancial/merchantsMoney/merchantsMoney.vue'
import walletDetail from '@/views/businessFinancial/merchantsMoney/walletDetail.vue'
import preloaded from '@/views/businessFinancial/merchantsMoney/preloaded.vue'



// 财务中心===商家提现处理
import withdrawal from '@/views/merchants/withdrawal.vue'


import NProgress from 'nprogress'
import 'nprogress/nprogress.css'
Vue.use(Router)

const router = new Router({
    // mode: "history",
    routes: [
        {
            path: '/',
            name: 'login',
            component: login
        },
        {
            path: '/home',
            name: 'home',
            component: home,
            children: [
                {
                  path: '',
                  component: openFinancial
                },
                {
                    path: '/openFinancial',
                    component: openFinancial
                },
                {
                    path: '/merchantsMoney',
                    component: merchantsMoney
                },
                {
                    path: '/withdrawal',
                    component: withdrawal
                },
                {
                    path: '/applyOpen',
                    component: applyOpen
                },
                {
                    path: '/chooseMerchants/:page',
                    component: chooseMerchants
                },
                {
                    path: '/walletDetail/:id',
                    component: walletDetail
                },
                {
                    path: '/preloaded',
                    component: preloaded
                },
                {
                    path: '/preloaded/:id',
                    component: preloaded
                },
            ]
        },
        {
            path: '*',
            name: '404',
            component: () => import('@/views/404.vue'),
        }
    ]
})

router.beforeEach((to, from, next) => {
    NProgress.start();
    // if(to.name != 'login') {
    //     let token = JSON.parse(window.sessionStorage.getItem('userItem'))||{};
    //     if(token.item) {
    //         next();
    //     } else {
    //         next({
    //             path: '/',
    //             query: {redirect: to.fullPath}  // 将跳转的路由path作为参数，登录成功后跳转到该路由
    //         });
    //     }
    // } else {
    //     next();
    // }
    next();

});
router.afterEach(() => {
    NProgress.done();
});

export default router


