      <el-breadcrumb-item>预充值</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 搜索 -->
    <el-card
      shadow="never"
      style="margin-bottom: 6px; height: 130px"
      class="box-card"
    >
      <el-row style="margin-bottom: 10px">
        <el-col :span="5">
          <div style="color: #333; font-size: 15px" class="demo-input-suffix">
            商家手机号：
            <el-input
              style="width: 200px"
              maxlength="11"
              v-model="search.phone"
              placeholder="请输入手机号"
            ></el-input>
          </div>
        </el-col>

        <el-col :span="5">
          <div style="color: #333; font-size: 15px" class="demo-input-suffix">
            商家名称：
            <el-input
              style="width: 200px"
              maxlength="11"
              v-model="search.name"
              placeholder="请输入商家名称"
            ></el-input>
          </div>
        </el-col>

        <el-col :span="6">
          <div style="color: #333; font-size: 15px" class="demo-input-suffix">
            交易类型：
            <el-select
              style="width: 200px"
              v-model="search.type"
              placeholder="请选择"
              clearable
            >
              <el-option
                v-for="item in type"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </div>
        </el-col>

        <el-col :span="6">
          <el-button @click="searchEvent" type="primary">查询</el-button>
          <el-button type="primary">导出</el-button>
          <el-button @click="dialogFormVisible = true" type="danger"
            >代充值</el-button
          >
        </el-col>
      </el-row>

      <el-row>
        <el-col :span="5">
          <div style="color: #333; font-size: 15px" class="demo-input-suffix">
            交易流水号：
            <el-input
              style="width: 200px"
              maxlength="11"
              v-model="search.number"
              placeholder="请输入商家名称"
            ></el-input>
          </div>
        </el-col>

        <el-col :span="5">
          <div style="color: #333; font-size: 15px" class="demo-input-suffix">
            商家等级：
            <el-select
              style="width: 200px"
              v-model="search.level"
              placeholder="请选择"
            >
              <el-option
                v-for="item in level"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </div>
        </el-col>

        <el-col :span="6">
          <!-- <div style="color: #333; font-size: 15px" class="demo-input-suffix">
            交易时间：
            <el-date-picker
              v-model="search.time"
              style="width: 240px"
              type="daterange"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
            >
            </el-date-picker>
          </div> -->

          <div class="block">
            <span class="demonstration">充值时间：</span>
            <el-date-picker
              v-model="search.time"
              type="datetimerange"
              range-separator="至"
              style="width: 240px"
              start-placeholder="开始日期"
              value-format="yyyy-MM-dd HH:mm"
              end-placeholder="结束日期">
            </el-date-picker>
          </div>
        </el-col>
      </el-row>
    </el-card>
    <!-- 表格 -->
    <el-card shadow="never" class="card_bottom box-card">
      <!-- 表格 -->
      <el-table
        height="calc(100vh - 350px)"
        highlight-current-row
        :data="tableData"
        v-loading="tableLoading"
      >
        <el-table-column fixed prop="transBatch" label="交易号" width="240">
        </el-table-column>
        <el-table-column prop="transMoney" label="交易金额" width="110">
        </el-table-column>
        <el-table-column prop="newMoney" label="交易后余额" width="160">
        </el-table-column>
        <el-table-column prop="transType" label="交易类型" width="140">
        </el-table-column>
        <el-table-column prop="addTimeString" label="交易时间" width="310">
        </el-table-column>
        <el-table-column prop="groupName" label="所属商家" width="130">
        </el-table-column>
        <el-table-column prop="sourceOrderNo" label="关联订单编号" width="160">
        </el-table-column>
        <el-table-column prop="memo" label="标签" min-width="130">
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <div class="pading">
        <el-pagination background layout="prev, pager, next" :total="paging.pages">
        </el-pagination>
      </div>
    </el-card>

    <!-- 代充值弹框 -->
    <el-dialog title="充值" width="600px" :visible.sync="dialogFormVisible">
      <el-form style="width: 400px" :model="form">
        <el-form-item label="充值商家" :label-width="formLabelWidth">
          <!-- <el-button @click="jumpPage('/chooseMerchants/preloaded')" type="primary">选择商家</el-button> -->
          <!-- <el-input
            :disabled="true"
            v-model="form.shangjia"
            autocomplete="off"
          ></el-input> -->
          <el-row :gutter="20">
            <el-col style="padding-left: 0" v-if="merchants" :span="14">
              <el-input
                :disabled="true"
                v-model="JSON.parse(merchants).groupName"
              ></el-input>
            </el-col>
            <el-col :span="4">
              <el-button
                @click="jumpPage('/chooseMerchants/preloaded')"
                type="primary"
                >选择商家</el-button
              >
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item label="充值金额" :label-width="formLabelWidth">
          <el-input v-model="form.money" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="充值方式" :label-width="formLabelWidth">
          <el-radio v-model="form.radio" label="1"
            >钱包余额充值&nbsp;&nbsp;&nbsp;&nbsp;<span style="color: #ff6666"
              >当前余额 200.00</span
            ></el-radio
          ><br />
          <el-radio v-model="form.radio" label="2">微信支付充值</el-radio>
        </el-form-item>
        <el-form-item label="备注" :label-width="formLabelWidth">
          <el-input
            type="textarea"
            placeholder="请输入内容"
            v-model="form.textarea"
            maxlength="30"
            show-word-limit
          >
          </el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <!-- <el-button type="primary" @click="dialogVisible = true"
          >立即充值</el-button> -->
        <el-button type="primary" @click="zhifu">立即充值</el-button>
      </div>
    </el-dialog>

    <!-- 代充值弹框余额不足情况显示二维码 -->
    <el-dialog title="微信支付" :visible.sync="dialogVisible" width="600px">
      <img
        class="erweima"
        src="https://dss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=1787157635,3078958303&fm=26&gp=0.jpg"
        alt=""
      />
      <div style="text-align: center; margin-top: 20px; font-size: 16px">
        微信扫码支付200.00元
      </div>

      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="dialogVisible = false"
          >确 定</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
import wx from "weixin-js-sdk";
import axios from "axios";
import { queryCzTrans, getOrder, getString } from "@/api/merchantsMoney.js";
export default {
  data() {
    return {
      search: {
        phone: "",
        name: "",
        level: 0,
        time: "",
        number: "",
        type: 0,
      },
      level: [
        {
          value: 0,
          label: "全部",
        },
        {
          value: 1,
          label: "一级（主）",
        },
        {
          value: 2,
          label: "一级（子）",
        },
        {
          value: 3,
          label: "二级",
        },
      ],
      type: [
        {
          value: 0,
          label: "全部",
        },
        {
          value: 1,
          label: "收入",
        },
        {
          value: 2,
          label: "支出",
        },
      ],
      paging: {},
      tableLoading: false,
      tableData: [],
      dialogFormVisible: false,
      form: {
        shangjia: "江苏常州",
        money: 0,
        radio: "1",
        textarea: "",
      },
      merchants: "",
      formLabelWidth: "120px",
      dialogVisible: false,
    };
  },
  methods: {
    jumpPage(e) {
      this.$router.push({ path: e });
    },
    // 搜索
    async searchEvent() {
      this.tableLoading = true;
      var n1 = await queryCzTrans({
        groupID: "1000",
        groupLevel: this.search.level,
        tradeType: this.search.type,
        beginTime: this.search.time[0],
        endTime: this.search.time[1],
        accountType: 0,
        groupName: this.search.name,
        groupPhone: this.search.phone,
        tradeNo: this.search.number,
        pageNumber: 1,
        pageSize: 10,
      });
      this.tableData = n1.data.data;
      this.paging = n1.data.pagination;
      this.tableLoading = false;
    },
    // 支付测试
    async zhifu() {
      // console.log(wx);
      // var n1 = await getOrder({
      //   "InvestGroupId": 1000,
      //   "PayType": 1,
      //   "payMoney": "0.01",
      //   "token": "bf706d48628a49f8b84e5329a96dfebe"
      // })
      // console.log(n1);

      axios
        .post(
          "https://portal.one5a.com/ClientAPITest/APIHandler.ashx?methodName=Finance_SubmitInvest",
          {
            InvestGroupId: 1000,
            PayType: 1,
            payMoney: "0.01",
            token: "bf706d48628a49f8b84e5329a96dfebe",
          }
        )
        .then(function (ress) {
          console.log(ress.Data);
          axios
            .post(
              "https://portal.one5a.com/ClientAPITest/APIHandler.ashx?methodName=Buiness_MiniPayFor",
              {
                OpenID: "oOuO25IEU2UrvI3SCNRAEeCWdhhU",
                OrderUID: ress.Data.UID,
                Price: ress.Data.PayMoney,
                StoreId: ress.Data.StoreID,
              }
            )
            .then(function (res) {
              alert('成功')
              console.log(1234);
              var data = JSON.parse(res.Data);
              // console.log(data);
              wx.config({
                debug: false, // 这里一般在测试阶段先用ture，等打包给后台的时候就改回false,
                appId: data.appId,
                timeStamp: data.timeStamp,
                nonceStr: data.nonceStr,
                package: data.package,
                signType: data.signType,
                paySign: data.paySign,
                jsApiList: ["chooseWXPay"],
              });
              console.log('config');
              wx.ready(function () {
                console.log('ready');
                wx.chooseWXPay({
                  appId: data.appId,
                  timestamp: data.timeStamp, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
                  nonceStr: data.nonceStr, // 支付签名随机串，不长于 32
                  package: data.package, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=\*\*\*）
                  signType: data.signType, // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
                  paySign: data.paySign, // 支付签名
                  success: function (res) {
                    console.log('支付成功');
                    // that.money = "";
                    // that.$router.go(0);
                  },
                  cancel: function (res) {
                    // 提示引用的是mint-UI里toast
                    console.log('已取消支付');
                  },
                  fail: function (res) {
                    console.log('支付失败，请重试');
                  },
                });
              });
            });
        });

      // wx.config({
      //   debug: false, // 这里一般在测试阶段先用ture，等打包给后台的时候就改回false,
      //   appId: data.appId,
      //   timeStamp: data.timeStamp,
      //   nonceStr: data.nonceStr,
      //   package: data.package,
      //   signType: data.signType,
      //   paySign: data.paySign,
      //   jsApiList: ['chooseWXPay']
      // })
      // wx.ready(function () {
      //   wx.chooseWXPay({
      //     appId: data.appId,
      //     timestamp: data.timeStamp, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
      //     nonceStr: data.nonceStr, // 支付签名随机串，不长于 32
      //     package: data.package, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=\*\*\*）
      //     signType: data.signType, // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
      //     paySign: data.paySign, // 支付签名
      //     success: function (res) {
      //       Toast('支付成功')
      //       that.money = ''
      //       that.$router.go(0)
      //     },
      //     cancel: function (res) { // 提示引用的是mint-UI里toast
      //       Toast('已取消支付')
      //     },
      //     fail: function (res) {
      //       Toast('支付失败，请重试')
      //     }
      //   })
      // })
    },
  },
  async created() {
    // 判断是否携带数据进行判断弹框是否调起
    if (this.$route.query.merchants) {
      this.dialogFormVisible = true;
      this.merchants = this.$route.query.merchants;
    }
    this.tableLoading = true;
    var n1 = await queryCzTrans({
      groupID: "1000",
      groupLevel: 0,
      tradeType: 0,
      beginTime: "",
      endTime: "",
      accountType: 0,
      groupName: "",
      groupPhone: "",
      tradeNo: "",
      pageNumber: 1,
      pageSize: 10,
    });
    this.tableData = n1.data.data;
    this.paging = n1.data.pagination;
    this.tableLoading = false;
  },
};
</script>

<style>
.card_bottom {
  width: 100%;
  height: calc(100vh - 260px);
  position: relative;
}
.pading {
  position: absolute;
  width: 200px;
  right: 240px;
  bottom: 20px;
}
.erweima {
  width: 400px;
  height: 400px;
  margin-left: 80px;
}
</style>


