<template>
  <div>

    <el-breadcrumb style="height:30px;" separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/merchantsMoney' }">商家资金管理</el-breadcrumb-item>
      <el-breadcrumb-item>钱包明细</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 搜索 -->
    <el-card
      shadow="never"
      style="margin-bottom: 6px; height: 130px"
      class="box-card"
    >
      <el-row style="margin-bottom: 10px">

        <el-col :span="5">
          <div style="color: #333; font-size: 15px" class="demo-input-suffix">
            商家手机号：
            <el-input
              style="width: 200px"
              maxlength="11"
              v-model="search.phone"
              placeholder="请输入手机号"
            ></el-input>
          </div>
        </el-col>


        <el-col :span="5">
          <div style="color: #333; font-size: 15px" class="demo-input-suffix">
            商家名称：
            <el-input
              style="width: 200px"
              maxlength="11"
              v-model="search.name"
              placeholder="请输入商家名称"
            ></el-input>
          </div>
        </el-col>

        

        
        <el-col :span="5">
          <div style="color: #333; font-size: 15px" class="demo-input-suffix">
            交易类型：
            <el-select
              style="width: 200px"
              v-model="search.type"
              placeholder="请选择"
              clearable
            >
              <el-option
                v-for="item in type"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </div>
        </el-col>

        <el-col :span="5">
          <div style="color: #333; font-size: 15px" class="demo-input-suffix">
            标签：
            <el-select
              style="width: 200px"
              v-model="search.label"
              placeholder="请选择"
              clearable
            >
              <el-option
                v-for="item in label"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </div>
        </el-col>

        <el-col :span="3">
          <el-button @click="searchEvent" type="primary">查询</el-button>
          <el-button type="primary">导出</el-button>
        </el-col>
      </el-row>

      <el-row>

        <el-col :span="5">
          <div style="color: #333; font-size: 15px" class="demo-input-suffix">
            商家交易号：
            <el-input
              style="width: 200px"
              maxlength="11"
              v-model="search.number"
              placeholder="请输入商家名称"
            ></el-input>
          </div>
        </el-col>

        <el-col :span="5">
          <div style="color: #333; font-size: 15px" class="demo-input-suffix">
            商家等级：
            <el-select
              style="width: 200px"
              v-model="search.level"
              placeholder="请选择"
            >
              <el-option
                v-for="item in level"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </div>
        </el-col>


        <el-col :span="6">
          <!-- <div style="color: #333; font-size: 15px" class="demo-input-suffix">
            充值时间：
            <el-date-picker
              v-model="search.time"
              style="width: 240px"
              type="daterange"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
            >
            </el-date-picker>
          </div> -->

          <div class="block">
            <span class="demonstration">充值时间：</span>
            <el-date-picker
              v-model="search.time"
              type="datetimerange"
              range-separator="至"
              style="width: 240px"
              start-placeholder="开始日期"
              value-format="yyyy-MM-dd HH:mm"
              end-placeholder="结束日期">
            </el-date-picker>
          </div>
        </el-col>

        
      </el-row>
    </el-card>
    <!-- 表格 -->
    <el-card shadow="never" class="card_bottom box-card">
      <!-- 表格 -->
      <el-table
        height="calc(100vh - 350px)"
        highlight-current-row
        :data="tableData"
        v-loading="tableLoading"
      >
        <el-table-column
          fixed
          prop="transBatch"
          label="交易流水号"
          width="180"
        >
        </el-table-column>
        <el-table-column
          prop="transType"
          label="交易类型"
          width="130"
        >
        </el-table-column>
        <el-table-column
          prop="tag"
          label="标签"
          width="100"
        >
        </el-table-column>
        <el-table-column
          prop="transMoney"
          label="交易金额"
          width="130"
        >
        </el-table-column>
        <el-table-column
          prop="newMoney"
          label="交易后金额"
          width="130"
        >
        </el-table-column>
        <el-table-column
          prop="addTimeString"
          label="交易时间"
          width="170"
        >
        </el-table-column>
        <el-table-column
          prop="sourceOrderNo"
          label="关联订单号\提现号"
          width="240"
        >
        </el-table-column>
        <el-table-column
          prop="groupName"
          label="所属商家"
          width="180"
        >
        </el-table-column>
        <el-table-column
          prop="groupLevel"
          label="等级"
          min-width="130"
        >
        </el-table-column>
        
      </el-table>
      <!-- 分页 -->
      <div class="pading">
        <el-pagination @current-change="pagingEvent" background layout="prev, pager, next" :total="paging.pages">
        </el-pagination>
      </div>
    </el-card>

  </div>
</template>

<script>
import {queryCzTrans} from '@/api/merchantsMoney.js'
export default {
  data() {
    return {
      search: {
        phone: "",
        name: "",
        level: 0,
        time: "",
        number:"",
        type:0,
        label:0
      },
      label:[
        {
          value: 0,
          label: "全部",
        },
        {
          value: 1,
          label: "换电卡利润",
        },
        {
          value: 2,
          label: "换电卡加价",
        },
        {
          value: 3,
          label: "二级提现",
        },
        {
          value: 4,
          label: "长租-租车收入",
        },
        {
          value: 5,
          label: "长租-逾期费",
        },
        {
          value: 6,
          label: "长租-定损费",
        },
        {
          value: 7,
          label: "换电卡二级利润",
        },
        {
          value: 8,
          label: "提现",
        },
        {
          value: 9,
          label: "转入预充值",
        },
      ],
      level: [
        {
          value: 0,
          label: "全部",
        },
        {
          value: 1,
          label: "一级（主）",
        },
        {
          value: 2,
          label: "一级（子）",
        },
        {
          value: 3,
          label: "二级",
        },
      ],
      type: [
        {
          value: 0,
          label: "全部",
        },
        {
          value: 1,
          label: "收入",
        },
        {
          value: 2,
          label: "支出",
        },
      ],
      tableLoading:false,
      paging:{},
      tableData: [],
    };
  },
  async created() {
    console.log('调试接口start');
    console.log(this.$route.params.id);
    this.tableLoading = true
    var n1 = await queryCzTrans({
      groupID: "1000",
      groupLevel: this.search.level,
      tradeType: this.search.type,
      beginTime: this.search.time[0],
      endTime: this.search.time[1],
      accountType: 1,
      groupName: this.search.name,
      groupPhone: this.search.phone,
      tradeNo: this.search.number,
      pageNumber: 1,
      pageSize: 10,
    })
    this.tableData = n1.data.data
    this.paging = n1.data.pagination
    this.tableLoading = false
  },
  methods:{
    // 分页
    pagingEvent(e){
      // e就是当前页码
      console.log(e);
    },
    // 搜索
    async searchEvent(){
      console.log(this.search);
      this.tableLoading = true
      var n1 = await queryCzTrans({
        groupID: "1000",
        groupLevel: this.search.level,
        tradeType: this.search.type,
        beginTime: this.search.time[0],
        endTime: this.search.time[1],
        accountType: 1,
        groupName: this.search.name,
        groupPhone: this.search.phone,
        tradeNo: this.search.number,
        pageNumber: 1,
        pageSize: 10,
      })
      this.tableData = n1.data.data
      this.paging = n1.data.pagination
      this.tableLoading = false
    }
  }
};
</script>

<style>
.card_bottom {
  width: 100%;
  height: calc(100vh - 260px);
  position: relative;
}
.pading {
  position: absolute;
  width: 200px;
  right: 240px;
  bottom: 20px;
}
</style>


